'use client';

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { useLanguage } from "@/contexts/language-context";
import { Volume2, Bell, User, Palette, VolumeX } from "lucide-react";
import { useSpeech } from "@/hooks/use-speech";

export default function SettingsPage() {
    const { language, setLanguage, t } = useLanguage();
    const { speak, stop, isSpeaking } = useSpeech();

    const handleSpeak = () => {
        if (isSpeaking) {
            stop();
        } else {
            const textToSpeak = `
                ${t('title', 'settings')}.
                ${t('description', 'settings')}.
                ${t('languageTitle', 'settings')}. ${t('languageDesc', 'settings')}.
                ${t('notificationsTitle', 'settings')}. ${t('notificationsDesc', 'settings')}. ${t('notif1', 'settings')}. ${t('notif2', 'settings')}. ${t('notif3', 'settings')}.
                ${t('accountTitle', 'settings')}. ${t('accountDesc', 'settings')}. ${t('editProfile', 'settings')}. ${t('changePhone', 'settings')}. ${t('logout', 'settings')}.
                ${t('themeTitle', 'settings')}. ${t('themeDesc', 'settings')}.
            `;
            speak({ text: textToSpeak, lang: language });
        }
    };


  return (
    <div className="space-y-6">
        <div className="flex justify-between items-center">
            <div>
                <h1 className="text-2xl font-bold">{t('title', 'settings')}</h1>
                <p className="text-muted-foreground">{t('description', 'settings')}</p>
            </div>
            <Button variant="outline" size="icon" onClick={handleSpeak}>
              {isSpeaking ? <VolumeX className="h-5 w-5" /> : <Volume2 className="h-5 w-5" />}
              <span className="sr-only">{isSpeaking ? t('stopReading', 'common') : t('readContent', 'settings')}</span>
            </Button>
        </div>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2"><Volume2 className="h-5 w-5" /> {t('languageTitle', 'settings')}</CardTitle>
          <CardDescription>{t('languageDesc', 'settings')}</CardDescription>
        </CardHeader>
        <CardContent>
           <div className="flex items-center space-x-2">
            <span>{t('langEn', 'settings')}</span>
            <Switch
              checked={language === 'ml'}
              onCheckedChange={(checked) => setLanguage(checked ? 'ml' : 'en')}
            />
            <span>{t('langMl', 'settings')}</span>
        </div>
        </CardContent>
      </Card>
      
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2"><Bell className="h-5 w-5" /> {t('notificationsTitle', 'settings')}</CardTitle>
          <CardDescription>{t('notificationsDesc', 'settings')}</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
            <div className="flex items-center justify-between">
                <Label htmlFor="task-reminders">{t('notif1', 'settings')}</Label>
                <Switch id="task-reminders" defaultChecked />
            </div>
             <div className="flex items-center justify-between">
                <Label htmlFor="community-updates">{t('notif2', 'settings')}</Label>
                <Switch id="community-updates" defaultChecked />
            </div>
             <div className="flex items-center justify-between">
                <Label htmlFor="promotional-offers">{t('notif3', 'settings')}</Label>
                <Switch id="promotional-offers" />
            </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2"><User className="h-5 w-5" /> {t('accountTitle', 'settings')}</CardTitle>
           <CardDescription>{t('accountDesc', 'settings')}</CardDescription>
        </CardHeader>
        <CardContent className="flex flex-col sm:flex-row gap-2">
            <Button>{t('editProfile', 'settings')}</Button>
            <Button variant="outline">{t('changePhone', 'settings')}</Button>
            <Button variant="destructive">{t('logout', 'settings')}</Button>
        </CardContent>
      </Card>

       <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2"><Palette className="h-5 w-5" /> {t('themeTitle', 'settings')}</CardTitle>
           <CardDescription>{t('themeDesc', 'settings')}</CardDescription>
        </CardHeader>
        <CardContent>
            <Select>
                <SelectTrigger className="w-[180px]">
                    <SelectValue placeholder={t('theme', 'settings')} />
                </SelectTrigger>
                <SelectContent>
                    <SelectItem value="light">{t('themeLight', 'settings')}</SelectItem>
                    <SelectItem value="dark">{t('themeDark', 'settings')}</SelectItem>
                    <SelectItem value="system">{t('themeSystem', 'settings')}</SelectItem>
                </SelectContent>
            </Select>
        </CardContent>
      </Card>
    </div>
  );
}
