'use client';

import { useState, useEffect, useCallback } from 'react';
import { useToast } from './use-toast';

interface SpeechOptions {
  text: string;
  lang?: string;
  voice?: SpeechSynthesisVoice;
}

export function useSpeech() {
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [isSupported, setIsSupported] = useState(false);
  const [voices, setVoices] = useState<SpeechSynthesisVoice[]>([]);
  const { toast } = useToast();

  useEffect(() => {
    if (typeof window !== 'undefined' && 'speechSynthesis' in window) {
      setIsSupported(true);
      const loadVoices = () => {
        setVoices(window.speechSynthesis.getVoices());
      };
      
      loadVoices();
      window.speechSynthesis.onvoiceschanged = loadVoices;

      // Cleanup function to cancel speech when component unmounts
      return () => {
        if(window.speechSynthesis.speaking) {
          window.speechSynthesis.cancel();
        }
        window.speechSynthesis.onvoiceschanged = null;
      };
    }
  }, []);

  const speak = useCallback(({ text, lang = 'en-US' }: SpeechOptions) => {
    if (!isSupported) {
      toast({
        title: 'Speech not supported',
        description: 'Your browser does not support text-to-speech.',
        variant: 'destructive',
      });
      return;
    }

    if (window.speechSynthesis.speaking) {
      window.speechSynthesis.cancel();
    }
    
    // Use a timeout to ensure the previous speech is cancelled before starting a new one
    setTimeout(() => {
        const utterance = new SpeechSynthesisUtterance(text);
        
        const mlVoice = voices.find(voice => voice.lang.startsWith('ml'));
        const targetLang = lang.startsWith('ml') ? 'ml-IN' : lang;

        utterance.lang = mlVoice ? mlVoice.lang : targetLang;
        const selectedVoice = voices.find(voice => voice.lang === utterance.lang);
        if(selectedVoice) {
            utterance.voice = selectedVoice;
        }

        utterance.onstart = () => setIsSpeaking(true);
        utterance.onend = () => setIsSpeaking(false);
        utterance.onerror = (event) => {
          console.error('SpeechSynthesis Error', event);
          setIsSpeaking(false);
           toast({
            title: 'Speech Error',
            description: 'Could not play the audio.',
            variant: 'destructive',
          });
        };

        window.speechSynthesis.speak(utterance);
    }, 100);

  }, [isSupported, voices, toast]);

  const stop = useCallback(() => {
    if (window.speechSynthesis.speaking) {
      window.speechSynthesis.cancel();
      setIsSpeaking(false);
    }
  }, []);

  return { isSupported, isSpeaking, speak, stop };
}
